<template>
    <el-dialog
            :title="title"
            v-model="dialogFormVisible"
            :close-on-click-modal="false"
            width="500px"
            @close="close"
    >
        <el-form ref="form" :model="form" :rules="rules" label-width="80px">
            <el-form-item label="域名" prop="domainValue">
                <el-input v-model.trim="form.domainValue" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="状态" prop="state">
                <el-select v-model="form.state" placeholder="状态">
                    <el-option label="启用" :value="1"></el-option>
                    <el-option label="禁用" :value="2"></el-option>
                </el-select>
            </el-form-item>
        </el-form>
        <template #footer>
            <el-button @click="close">取 消</el-button>
            <el-button type="primary" @click="save">确 定</el-button>
        </template>
    </el-dialog>
</template>

<script>
    import { saveDomain, updateDomain } from "@/api/dnspod/domain"
    export default {
        name: "domainEdit",
        data() {
            return {
                form: {
                    domainValue: '',
                    state: 1
                },
                rules: {
                    domainValue: [{ required: true, trigger: "blur", message: "请输入域名" }],
                },
                title: "",
                operate: false,
                dialogFormVisible: false,
                refreshTable: false,
            }
        },
        methods: {
            showEdit(row) {
                if (!row) {
                    this.title = "添加域名";
                    this.operate = false;
                } else {
                    this.title = "修改域名";
                    this.operate = true;
                    this.form = Object.assign({}, row);
                }
                this.dialogFormVisible = true;
                this.refreshTable = false;
            },
            close() {
                this.$refs["form"].resetFields();
                this.form = this.$options.data().form;
                this.dialogFormVisible = false;
                if(this.refreshTable) {
                    this.$emit("reloadTable");
                }
            },
            async save() {
                this.$refs["form"].validate(async (valid) => {
                    if (valid) {
                        this.refreshTable = true;
                        if (this.operate) {
                            await updateDomain(this.form).then((res) => {
                                if (res.code == 200) {
                                    this.$message({ type: "success", message: res.message });
                                    this.close();
                                } else {
                                    this.$message({ type: "error", message: res.message });
                                }
                            });
                        } else {
                            await saveDomain(this.form).then((res) => {
                                if (res.code == 200) {
                                    this.$message({ type: "success", message: res.message });
                                    this.close();
                                } else {
                                    this.$message({ type: "error", message: res.message });
                                }
                            });
                        }
                    } else {
                        return false;
                    }
                });
            },
        }
    }
</script>

