<template>
    <el-dialog
            :title="title"
            v-model="dialogFormVisible"
            :close-on-click-modal="false"
            width="1000px"
            @close="close"
    >
        <div class="domain-container">
            <el-row class="query-form">
                <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12" class="left-panel">
                    <el-button type="primary" @click="handleAdd">
                        <i class="fa fa-edit"></i> 添加
                    </el-button>
                    <el-button @click="reloadTable">
                        <i class="fa fa-refresh"></i> 刷新
                    </el-button>
                </el-col>
                <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12" class="right-panel">
                    <el-form :inline="true" :model="searchForm" @submit.prevent>
                        <el-form-item style="width: 150px">
                            <el-input
                                    v-model.trim="searchForm.domainValue"
                                    placeholder="域名"
                                    clearable
                                    @keyup.enter="search"
                            />
                        </el-form-item>
                        <el-form-item style="width: 100px">
                            <el-select
                                    v-model="searchForm.state"
                                    placeholder="状态"
                                    @change="search"
                                    clearable
                            >
                                <el-option :value="1" label="启用"></el-option>
                                <el-option :value="2" label="禁用"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="search">
                                <i class="fa fa-search"></i> 查询
                            </el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
            <el-table
                    v-loading="listLoading"
                    :data="subDomains"
                    :element-loading-text="elementLoadingText"
            >
                <el-table-column show-overflow-tooltip label="序号">
                    <template #default="scope">
                        {{ (page.curPage - 1) * page.pageSize + scope.$index + 1 }}
                    </template>
                </el-table-column>
                <el-table-column prop="subDomainValue" label="域名">
                    <template #default="{ row }">
                        <el-tag effect="dark" >{{row.subDomainValue}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="createTime" label="创建时间"></el-table-column>
                <el-table-column prop="updateTime" label="修改时间"></el-table-column>
                <el-table-column prop="state" label="状态" width="80" fixed="right">
                    <template #default="{ row }">
                        <el-popconfirm
                                title="你确定要修改状态？"
                                @confirm="updateSubDomainState(row)"
                        >
                            <template #reference>
                                <el-switch
                                        :value="row.state"
                                        :active-value="1"
                                        :inactive-value="2"
                                        style="--el-switch-on-color: #13ce66; --el-switch-off-color: #ff4949"
                                ></el-switch>
                            </template>
                        </el-popconfirm>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="100" fixed="right">
                    <template #default="{ row }">
                        <el-button type="text" @click="handleEdit(row)">编辑</el-button>
                        <el-button type="text" style="color: #F56C6C" @click="handleDelete(row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                    background
                    small
                    :current-page="page.curPage"
                    :page-size="page.pageSize"
                    :layout="layout"
                    :total="page.total"
                    :page-sizes="[8, 10, 20, 30, 40, 50, 100]"
                    @size-change="handleChangeSize"
                    @current-change="handleChangePage"
            ></el-pagination>
        </div>
        <sub-domain-edit ref="edit" @reloadTable="pageSubDomain" />
    </el-dialog>
</template>

<script>
    import { pageSubDomain, deleteSubDomain, updateSubDomainState } from "@/api/dnspod/domain"
    import subDomainEdit from "./components/subDomainEdit";
    export default {
        name: "SubDomain",
        components: {
            subDomainEdit
        },
        data() {
            return {
                title: '子域名列表',
                operate: false,
                dialogFormVisible: false,
                listLoading: true,
                layout: "total, sizes, prev, pager, next, jumper",
                elementLoadingText: "正在加载...",
                page: {
                    curPage: 1,
                    pageSize: 10,
                    total: 0,
                },
                subDomains: [],
                searchForm: {
                    domainValue: "",
                    state: "",
                    domainId: '',
                },
                domain: {},
            }
        },
        methods: {
            showSubDomain(row) {
                this.title = "【" + row.domainValue + "】子域名列表";
                this.searchForm.domainId = row.id
                this.domain = row
                this.pageSubDomain();
                this.dialogFormVisible = true;
            },
            pageSubDomain() {
                this.listLoading = true;
                this.searchForm.curPage = this.page.curPage;
                this.searchForm.pageSize = this.page.pageSize;
                pageSubDomain(this.searchForm).then((res) => {
                    if (res.code == 200) {
                        this.subDomains = res.data.records
                        this.page.total = res.data.total
                    } else {
                        if (this.page.curPage != 1) {
                            this.search();
                        } else {
                            this.subDomains = []
                            this.page.total = 0
                        }
                    }
                    this.listLoading = false;
                })
            },
            reloadTable() {
                this.searchForm = this.$options.data().searchForm;
                this.page.curPage = 1;
                this.pageSubDomain();
            },
            search() {
                this.page.curPage = 1;
                this.pageSubDomain();
            },
            handleChangePage(number) {
                this.page.curPage = number;
                this.pageSubDomain();
            },
            handleChangeSize(number) {
                this.page.pageSize = number;
                this.pageSubDomain();
            },
            handleAdd() {
                this.$refs["edit"].showEdit(this.domain);
            },
            handleEdit(row) {
                this.$refs["edit"].showEdit(this.domain, row);
            },
            handleDelete(row) {
                this.$confirm("你确定要删除域名【"+row.subDomainValue+"】？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning",
                    "close-on-click-modal": false,
                }).then(() => {
                    deleteSubDomain(row.id).then((res) => {
                        if (res.code == 200) {
                            this.pageSubDomain()
                            this.$message({ type: "success", message: res.message });
                        } else {
                            this.$message({ type: "error", message: res.message });
                        }
                    })
                })
            },
            updateSubDomainState(row) {
                const state = row.state == 1 ? 2 : 1;
                updateSubDomainState(row.id, state).then((res) => {
                    if(res.code == 200) {
                        row.state = state
                        this.$message({ type: "success", message: res.message });
                    } else {
                        this.$message({ type: "error", message: res.message });
                    }
                })
            },
        }
    }
</script>

