import request from "@/utils/request";

const basicUrl = "/admin/dnspod"

/************  域名  *****************/
export function pageDomains(data) {
    return request({
        url: basicUrl + "/pageDomains",
        method: "post",
        params: {
            domainValue: data.domainValue,
            state: data.state,
            curPage: data.curPage,
            pageSize: data.pageSize
        }
    });
}

export function deleteDomain(id) {
    return request({
        url: basicUrl + "/deleteDomain/" + id,
        method: "get",
    });
}

export function saveDomain(data) {
    return request({
        url: basicUrl + "/saveDomain",
        method: "post",
        data
    });
}

export function updateDomain(data) {
    return request({
        url: basicUrl + "/updateDomain",
        method: "post",
        data
    });
}

export function updateDomainState(id, state) {
    return request({
        url: basicUrl + "/updateDomainState",
        method: "post",
        params: {
            id: id,
            state: state
        }
    });
}

/************  子域名  *****************/
