<template>
    <div class="domain-container">
        <el-row class="query-form">
            <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12" class="left-panel">
                <el-button type="primary" @click="handleAdd">
                    <i class="fa fa-edit"></i> 添加
                </el-button>
                <el-button @click="reloadTable">
                    <i class="fa fa-refresh"></i> 刷新
                </el-button>
            </el-col>
            <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12" class="right-panel">
                <el-form :inline="true" :model="searchForm" @submit.prevent>
                    <el-form-item style="width: 150px">
                        <el-input
                                v-model.trim="searchForm.domainValue"
                                placeholder="域名"
                                clearable
                                @keyup.enter="search"
                        />
                    </el-form-item>
                    <el-form-item style="width: 100px">
                        <el-select
                                v-model="searchForm.state"
                                placeholder="状态"
                                @change="search"
                                clearable
                        >
                            <el-option :value="1" label="启用"></el-option>
                            <el-option :value="2" label="禁用"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="search">
                            <i class="fa fa-search"></i> 查询
                        </el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <el-table
                v-loading="listLoading"
                :data="domains"
                :element-loading-text="elementLoadingText"
        >
            <el-table-column show-overflow-tooltip label="序号">
                <template #default="scope">
                    {{ (page.curPage - 1) * page.pageSize + scope.$index + 1 }}
                </template>
            </el-table-column>
            <el-table-column prop="domainValue" label="域名">
                <template #default="{ row }">
                    <el-button type="primary" link @click="showSubDomain(row)">{{row.domainValue}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="createTime" label="创建时间"></el-table-column>
            <el-table-column prop="updateTime" label="修改时间"></el-table-column>
            <el-table-column prop="state" label="状态" width="80" fixed="right">
                <template #default="{ row }">
                    <el-popconfirm
                            title="你确定要修改状态？"
                            @confirm="updateDomainState(row)"
                    >
                        <template #reference>
                            <el-switch
                                    :value="row.state"
                                    :active-value="1"
                                    :inactive-value="2"
                                    style="--el-switch-on-color: #13ce66; --el-switch-off-color: #ff4949"
                            ></el-switch>
                        </template>
                    </el-popconfirm>
                </template>
            </el-table-column>
            <el-table-column label="操作" width="180" fixed="right">
                <template #default="{ row }">
                    <el-button type="warning" link @click="showSubDomain(row)">子域名</el-button>
                    <el-button type="primary" link @click="handleEdit(row)">编辑</el-button>
                    <el-button type="danger" link @click="handleDelete(row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
                background
                small
                :current-page="page.curPage"
                :page-size="page.pageSize"
                :layout="layout"
                :total="page.total"
                :page-sizes="[8, 10, 20, 30, 40, 50, 100]"
                @size-change="handleChangeSize"
                @current-change="handleChangePage"
        ></el-pagination>
    </div>
    <domain-edit ref="edit" @reloadTable="pageDomains" />
    <sub-domain ref="subDomain" />
</template>

<script>
    import { pageDomains, deleteDomain, updateDomainState } from "@/api/dnspod/domain"
    import domainEdit from "./components/domainEdit";
    import subDomain from "./subDomain";
    export default {
        name: "domain",
        components: {
            domainEdit,
            subDomain
        },
        data() {
            return {
                listLoading: true,
                layout: "total, sizes, prev, pager, next, jumper",
                elementLoadingText: "正在加载...",
                page: {
                    curPage: 1,
                    pageSize: 10,
                    total: 0,
                },
                domains: [],
                searchForm: {
                    domainValue: "",
                    state: "",
                },
            }
        },
        created() {
          this.pageDomains()
        },
        methods: {
            pageDomains() {
                this.listLoading = true;
                this.searchForm.curPage = this.page.curPage;
                this.searchForm.pageSize = this.page.pageSize;
                pageDomains(this.searchForm).then((res) => {
                    if (res.code == 200) {
                        this.domains = res.data.records
                        this.page.total = res.data.total
                    } else {
                        if (this.page.curPage != 1) {
                            this.search();
                        } else {
                            this.domains = []
                            this.page.total = 0
                        }
                    }
                    this.listLoading = false;
                })
            },
            reloadTable() {
                this.searchForm = this.$options.data().searchForm;
                this.page.curPage = 1;
                this.pageDomains();
            },
            search() {
                this.page.curPage = 1;
                this.pageDomains();
            },
            handleChangePage(number) {
                this.page.curPage = number;
                this.pageDomains();
            },
            handleChangeSize(number) {
                this.page.pageSize = number;
                this.pageDomains();
            },
            handleAdd() {
                this.$refs["edit"].showEdit();
            },
            handleEdit(row) {
                this.$refs["edit"].showEdit(row);
            },
            handleDelete(row) {
                this.$confirm("你确定要删除域名【"+row.domainValue+"】？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning",
                    "close-on-click-modal": false,
                }).then(() => {
                    deleteDomain(row.id).then((res) => {
                        if (res.code == 200) {
                            this.pageDomains()
                            this.$message({ type: "success", message: res.message });
                        } else {
                            this.$message({ type: "error", message: res.message });
                        }
                    })
                })
            },
            updateDomainState(row) {
                const state = row.state == 1 ? 2 : 1;
                updateDomainState(row.id, state).then((res) => {
                    if(res.code == 200) {
                        row.state = state
                        this.$message({ type: "success", message: res.message });
                    } else {
                        this.$message({ type: "error", message: res.message });
                    }
                })
            },
            showSubDomain(row) {
                this.$refs["subDomain"].showSubDomain(row);
            }
        }
    }
</script>

<style scoped>

</style>
